
.node {
  width: 40px;
  height: 40px;
  outline: 1px solid rgb(175, 216, 248);
  display: inline-block;
  text-align: center;
  vertical-align: middle;
}

.node-finish {
  background-color: red;
}

.node-start {
  background-color: green;
}
.node-dev {
  background-color: rgb(77, 109, 197);
}

.node-prev {
  background-color: yellow;
}

.if_statement_1{
  position: absolute;
  background-color:rgb(199, 199, 235);
  margin-top:0%;
  width:400px;
  color:black;
  font-weight: bold;
  border-color: black;
  border-width: 3px;
  border-style: solid;
}

